import styled from '@emotion/styled'
import { Button, CircularProgress, TextField } from '@mui/material'
import { Box } from '@mui/system'
import dayjs from 'dayjs'
import { useEffect, useState } from 'react'
import { toast } from 'react-toastify'

const checkDuration = (startDate, endDate) => {
  const limitDate = dayjs(endDate).subtract(60, 'day')
  return dayjs(startDate).isBefore(dayjs(limitDate))
}

export default function SearchForm({
  children,
  requestFn,
  ctx,
  startLabel,
  endLabel,
  noLimit = false,
}) {
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [startDate, setStartDate] = useState(
    dayjs().subtract(7, 'day').format('YYYY-MM-DD')
  )
  const [endDate, setEndDate] = useState(dayjs().format('YYYY-MM-DD'))

  const submitHandler = async (e) => {
    e?.preventDefault()
    if (startDate > endDate) {
      toast.error('起始日期不得晚於結束日期')
    } else if (checkDuration(startDate, endDate) && !noLimit) {
      toast.error('日期區間須為60天以內')
    } else {
      setIsSubmitting(true)
      ctx.setStartDate(startDate)
      ctx.setEndDate(endDate)
      ctx.setCurrentTime(dayjs().format('YYYY/MM/DD HH:mm:ss'))
      ctx.setIsSubmitting(true)
      await requestFn(startDate, endDate)
      setTimeout(() => {
        ctx.setIsSubmitting(false)
        setIsSubmitting(false)
      }, 1000)
    }
  }

  useEffect(() => {
    if (ctx.needRefresh) {
      submitHandler()
    }
  }, [ctx.needRefresh])

  return (
    <Form onSubmit={submitHandler}>
      <InputsContainer>
        <TextField
          variant="standard"
          type="date"
          label={startLabel ? startLabel : '班次起始日期'}
          fullWidth
          InputLabelProps={{ shrink: true }}
          value={startDate}
          onChange={(e) => {
            setStartDate(e.target.value)
          }}
        />
        <TextField
          variant="standard"
          type="date"
          label={endLabel ? endLabel : '班次結束日期'}
          fullWidth
          InputLabelProps={{ shrink: true }}
          value={endDate}
          onChange={(e) => {
            setEndDate(e.target.value)
          }}
        />
        {children}
      </InputsContainer>
      <Button variant="contained" type="submit" disabled={isSubmitting}>
        {isSubmitting ? <CircularProgress size={24} /> : '查詢'}
      </Button>
    </Form>
  )
}

export const Form = styled.form`
  border: 1px solid rgba(224, 224, 224, 1);
  border-radius: 4px;
  padding: 16px;
  display: grid;
  grid-template-columns: 1fr 100px;
  align-items: center;
  gap: 48px;
`

export const InputsContainer = styled(Box)`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 36px;
`
