export default function responseHandler(resp) {
  const { data } = resp
  let error
  switch (data?.type) {
    case 0:
      break
    case 1:
      error = new Error('fail')
      error.resp = resp
      error.data = data.data
      break
    case 2:
      window.location = process.env.REACT_APP_BASE_URL + '/signin'
      break
    default:
      error = new Error('exception')
      error.resp = resp
      error.data = data.data
      break
  }
  if (error) {
    throw error
  } else {
    return data.data
  }
}
