import { Card, CardContent, Divider, Typography } from '@mui/material'

export default function Widget(props) {
  return (
    <Card sx={props.sx}>
      <Typography
        variant="h6"
        component="div"
        sx={{
          paddingLeft: 2,
          paddingTop: 1,
          paddingBottom: 1,
        }}
      >
        {props.title}
      </Typography>
      <Divider />
      <CardContent>{props.children}</CardContent>
    </Card>
  )
}
