import styled from '@emotion/styled'
import {
  Checkbox,
  FormControlLabel,
  FormGroup,
  MenuItem,
  TextField,
} from '@mui/material'
import { useContext, useEffect, useState } from 'react'
import { toast } from 'react-toastify'
import { ExceptionRecordContext } from '../../page/R89/ExceptionRecord'
import { getRecord, getSetting } from '../../util/api/r89/exceptionRecord'
import SearchForm from '../Rongfu/SearchForm'

export default function ExceptionRecordSearchForm() {
  const ctx = useContext(ExceptionRecordContext)
  const [position, setPosition] = useState([])
  const [isException, setIsException] = useState(true)
  const [positionOptions, setPositionOptions] = useState([])
  const [endpoint, setEndpoint] = useState(null)

  useEffect(() => {
    getSetting().then((data) => {
      const { lane, ng_active, ng_ware } = data
      const positions = []
      for (const key in lane) {
        if (lane[key]) {
          positions.push({
            label: lane[key].name,
            value: lane[key].combination,
          })
        }
      }
      setPositionOptions(positions)
      ctx.setPositionOptions(positions)
      ctx.setStepMap(ng_active)
      ctx.setEquipmentMap(ng_ware)
    })
  }, [])

  useEffect(() => {
    const handleMessage = (e) => {
      setEndpoint(e.data)
      console.log(e.data)
    }
    window.addEventListener('message', handleMessage)
    return () => {
      window.removeEventListener('message', handleMessage)
    }
  }, [])

  const positionChangeHandler = (e) => {
    setPosition(e.target.value)
  }

  const requestFn = async (startDate, endDate) => {
    try {
      ctx.setPosition(position)
      ctx.setIsException(isException)
      let data
      if (endpoint) {
        data = await getRecord(
          startDate,
          endDate,
          position,
          isException,
          endpoint
        )
      } else {
        data = await getRecord(startDate, endDate, position, isException)
      }
      ctx.setRecord(data)
    } catch (err) {
      ctx.setRecord([])
      toast.error(err.data)
    }
  }

  return (
    <SearchForm
      ctx={ctx}
      requestFn={requestFn}
      startLabel="起始日期"
      endLabel="結束日期"
    >
      <TextField
        label="車道"
        select
        required
        aria-required
        SelectProps={{
          multiple: true,
          onChange: positionChangeHandler,
          value: position,
        }}
        variant="standard"
      >
        {positionOptions.map((option) => (
          <MenuItem key={option.value} value={option.value}>
            {option.label}
          </MenuItem>
        ))}
      </TextField>
      <StyledFormGroup>
        <FormControlLabel
          control={<Checkbox defaultChecked />}
          label="只查詢異常"
          onChange={(e) => setIsException(e.target.checked)}
        />
      </StyledFormGroup>
    </SearchForm>
  )
}

const StyledFormGroup = styled(FormGroup)`
  align-self: flex-end;
`
