import axios from 'axios'
import { API_DOMAIN, USE_API_KEY } from '../constants'

const config = {
  baseURL: API_DOMAIN,
  withCredentials: !USE_API_KEY,
}

if (USE_API_KEY) {
  config.params = {
    stkey: process.env.REACT_APP_API_KEY,
  }
}
const api = axios.create(config)
export default api
