import styled from '@emotion/styled'
import { Button } from '@mui/material'
import { useRef } from 'react'
import { API_DOMAIN, DEV_MODE } from '../util/constants'

export default function ExcelButton({ data }) {
  const hiddenFormElement = useRef(null)

  const downloadHandler = () => {
    hiddenFormElement.current.submit()
  }
  return (
    <Container>
      <HiddenForm
        action={`${API_DOMAIN}/api/excel/download`}
        method="post"
        target="download_target"
        ref={hiddenFormElement}
      >
        <HiddenInput name="data" value={JSON.stringify(data)} />
        {process.env.REACT_APP_API_KEY && DEV_MODE && (
          <HiddenInput name="stkey" value={process.env.REACT_APP_API_KEY} />
        )}
        <Iframe name="download_target" />
      </HiddenForm>
      <Button variant="contained" sx={{ margin: 2 }} onClick={downloadHandler}>
        下載為 EXCEL 格式
      </Button>
    </Container>
  )
}

export const Container = styled.div`
  position: relative;
`

export const HiddenForm = styled.form`
  width: 0;
  height: 0;
  border: none;
  position: absolute;
  top: 0;
  left: -10000;
`
export const Iframe = styled.iframe`
  width: 0;
  height: 0;
  border: none;
  position: absolute;
  top: 0;
  left: -10000;
`
export const HiddenInput = styled.input`
  width: 0;
  height: 0;
  border: none;
  position: absolute;
  top: 0;
  left: -10000;
`
