import { API_DOMAIN, DEV_MODE } from '../../constants'
import api from '../api'
import responseHandler from '../responseHandler'

export function getRecord(
  start_date,
  end_date,
  position,
  isException,
  endpoint = '/api/custom/report'
) {
  return api
    .post(endpoint, {
      start_date: `${start_date} 00:00:00`,
      end_date: `${end_date} 23:59:59`,
      combs: position,
      only_ng: isException,
    })
    .then(responseHandler)
    .then((data) => {
      const sortedData = data.sort(
        (a, b) => new Date(b.start_time) - new Date(a.start_time)
      )
      sortedData.forEach((item, i) => {
        item.id = i + 1
      })
      return sortedData
    })
}

export function getVideo(start_time, combination) {
  return api.get(`/api/r89/report/video-download`, {
    params: {
      start_time,
      combination,
    },
  })
}

export function getVideoByForm(start_time, combination) {
  const form = document.createElement('form')
  const startTimeInput = document.createElement('input')
  const combinationInput = document.createElement('input')
  const stkeyInput = document.createElement('input')
  const iframe = document.createElement('iframe')
  form.action = `${API_DOMAIN}/api/r89/report/video-download`
  form.method = 'get'
  form.target = 'download_target'

  startTimeInput.name = 'start_time'
  startTimeInput.value = start_time

  combinationInput.name = 'combination'
  combinationInput.value = combination

  iframe.name = 'download_target'
  form.appendChild(startTimeInput)
  form.appendChild(combinationInput)
  form.appendChild(iframe)
  if (process.env.REACT_APP_API_KEY && DEV_MODE) {
    stkeyInput.name = 'stkey'
    stkeyInput.value = process.env.REACT_APP_API_KEY
    form.appendChild(stkeyInput)
  }
  document.body.appendChild(form)
  form.submit()
  form.remove()
}

