import { DataGrid } from '@mui/x-data-grid'
import zhTW from '../constants/dataGridLocale-zhTW'

export default function MTable(props) {
  function handlePageChange() {
    document.querySelector('.MuiDataGrid-virtualScroller').scrollTop = 0
  }

  return (
    <DataGrid onPageChange={handlePageChange} {...props} localeText={zhTW} />
  )
}
