import React, { useState } from 'react'
import ExceptionRecordSearchForm from '../../components/R89/SearchForm'
import ExceptionRecordSearchResult from '../../components/R89/SearchResult'
import Widget from '../../components/Widget'

export const ExceptionRecordContext = React.createContext({
  startDate: null,
  endDate: null,
  currentTime: null,
  record: null,
  isSubmitting: false,
  position: null,
  positionOptions: null,
  isException: null,
  stepMap: null,
  equipmentMap: null,
  setStartDate: () => {},
  setEndDate: () => {},
  setCurrentTime: () => {},
  setRecord: () => {},
  setIsSubmitting: () => {},
  setPosition: () => {},
  setPositionOptions: () => {},
  setIsException: () => {},
  setStepMap: () => {},
  setEquipmentMap: () => {},
})

export default function ExceptionRecord() {
  const [startDate, setStartDate] = useState(null)
  const [endDate, setEndDate] = useState(null)
  const [currentTime, setCurrentTime] = useState(null)
  const [record, setRecord] = useState([])
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [position, setPosition] = useState([])
  const [isException, setIsException] = useState(true)
  const [positionOptions, setPositionOptions] = useState([])
  const [stepMap, setStepMap] = useState({})
  const [equipmentMap, setEquipmentMap] = useState({})

  return (
    <ExceptionRecordContext.Provider
      value={{
        startDate,
        endDate,
        currentTime,
        record,
        isSubmitting,
        position,
        positionOptions,
        isException,
        stepMap,
        equipmentMap,
        setStartDate,
        setEndDate,
        setCurrentTime,
        setRecord,
        setIsSubmitting,
        setPosition,
        setPositionOptions,
        setIsException,
        setStepMap,
        setEquipmentMap,
      }}
    >
      <Widget title="查詢" sx={{ mb: 4 }}>
        <ExceptionRecordSearchForm />
      </Widget>
      {startDate && (
        <Widget title="查詢結果">
          <ExceptionRecordSearchResult />
        </Widget>
      )}
    </ExceptionRecordContext.Provider>
  )
}
