import styled from '@emotion/styled'
import { Videocam } from '@mui/icons-material'
import { Box, Button, LinearProgress, Tooltip, Typography } from '@mui/material'
import dayjs from 'dayjs'
import _ from 'lodash'
import { useContext, useEffect, useState } from 'react'
import { toast } from 'react-toastify'
import { ExceptionRecordContext } from '../../page/R89/ExceptionRecord'
import { getVideo, getVideoByForm } from '../../util/api/r89/exceptionRecord'
import ExcelButton from '../ExcelButton'
import MTable from '../MTable'

function getForm(record, columns) {
  const form = {
    fileName: `異常紀錄_${dayjs().format('YYYYMMDDHHmmss')}`,
    format: _.fill(Array(columns.length - 1), 'text'),
    header: columns.map((item, i) => item.headerName).slice(0, -1),
    matrix: [],
  }
  record.forEach((data) => {
    const array = []
    form.header.forEach((headerName) => {
      const filteredColumn = columns.find(
        (column) => column.headerName === headerName
      )
      if (filteredColumn?.valueFormatter !== undefined) {
        array.push(
          filteredColumn.valueFormatter({ value: data[filteredColumn.field] })
        )
      } else if (filteredColumn?.valueGetter !== undefined) {
        array.push(filteredColumn.valueGetter({ row: data }))
      } else {
        array.push(data[filteredColumn.field])
      }
    })
    form.matrix.push(array)
  })
  return form
}

function msToMinSec(params) {
  if (!params.value) {
    return '---'
  } else {
    const ms = params.value
    let min = Math.floor(ms / 60000)
    let sec = _.round((ms % 60000) / 1000, 2)
    if (sec === 60) {
      min += 1
      sec = 0
    }
    let timeFormat = ''
    if (min > 0) {
      timeFormat += `${min}min`
    }
    timeFormat += ` ${sec}s`
    return timeFormat
  }
}

export default function ExceptionRecordSearchResult() {
  const [excelData, setExcelData] = useState({})
  const [searchPosition, setSearchPosition] = useState('')
  const ctx = useContext(ExceptionRecordContext)
  const columnsBase = [
    {
      field: 'start_time',
      headerName: '識別時間',
      width: 170,
    },
    {
      field: 'combination',
      headerName: '車道',
      width: 90,
      valueFormatter: (params) => {
        const optionObject = ctx.positionOptions.find(
          (obj) => obj.value === params.value
        )
        return params.value ? optionObject.label : '---'
      },
    },
    {
      field: 'cycle_time_ms',
      headerName: '作業總時間',
      width: 120,
      type: 'number',
      valueFormatter: (params) => msToMinSec(params),
    },
    {
      field: 'missing_step',
      headerName: '異常動作',
      width: 170,
      valueFormatter: (params) => {
        if (!params.value) {
          return '---'
        }
        const steps = params.value.split('+')
        const stepsWithMs = steps.map(
          (step) => `step0${step.trim().split('p')[1]}_ms`
        )
        return stepsWithMs.map((step) => ctx.stepMap[step]).join('\n')
      },
      renderCell: (params) => {
        if (!params.value) {
          return '---'
        }
        const steps = params.value.split('+')
        const stepsWithMs = steps.map(
          (step) => `step0${step.trim().split('p')[1]}_ms`
        )
        return (
          <Box sx={{ display: 'flex', flexWrap: 'wrap', padding: '4px 0px' }}>
            {stepsWithMs.map((step) => (
              <Typography sx={{ width: '100%' }}>
                {ctx.stepMap[step]}
              </Typography>
            ))}
          </Box>
        )
      },
    },
    {
      field: 'false_triggered',
      headerName: '穿戴異常次數',
      width: 120,
      type: 'number',
    },
    {
      field: 'equipment_error',
      headerName: '裝備異常',
      width: 120,
      valueGetter: (params) => {
        if (Number(params.row.false_triggered) === 0) {
          return '---'
        } else {
          const equipments = Object.keys(ctx.equipmentMap)
          const errorTimes = equipments.map((equipment) =>
            params.row ? params.row[equipment] : 0
          )
          const maxTimes = _.max(errorTimes)
          const errorEquipments = []
          equipments.forEach((equipment) => {
            if (params.row[equipment] < maxTimes) {
              errorEquipments.push(ctx.equipmentMap[equipment])
            }
          })
          return errorEquipments.join('\n')
        }
      },
      renderCell: (params) => {
        if (Number(params.row.false_triggered) === 0) {
          return (
            <Box>
              <Typography>---</Typography>
            </Box>
          )
        } else {
          const equipments = Object.keys(ctx.equipmentMap)
          const errorTimes = equipments.map((equipment) =>
            params.row ? params.row[equipment] : 0
          )
          const maxTimes = _.max(errorTimes)
          const errorEquipments = []
          equipments.forEach((equipment) => {
            if (params.row[equipment] < maxTimes) {
              errorEquipments.push(ctx.equipmentMap[equipment])
            }
          })
          return (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', padding: '4px 0px' }}>
              {errorEquipments.map((equipment) => (
                <Typography sx={{ width: '100%' }}>{equipment}</Typography>
              ))}
            </Box>
          )
        }
      },
    },
    {
      field: 'video',
      headerName: '影片下載',
      width: 120,
      sortable: false,
      renderCell: (params) => {
        return (
          <Box sx={{ textAlign: 'center', width: '100%' }}>
            <Tooltip title="下載影像">
              <Button
                variant="outlined"
                onClick={() => {
                  clickHandler(params.row.start_time, params.row.combination)
                }}
              >
                <Videocam />
              </Button>
            </Tooltip>
          </Box>
        )
      },
    },
  ]
  const [columns, setColumns] = useState(() => {
    const newColumns = [...columnsBase]
    const keys = Object.keys(ctx.stepMap)
    keys.forEach((key, i) => {
      newColumns.splice(2 + i, 0, {
        field: key,
        headerName: `動作${i + 1}:${ctx.stepMap[key]}`,
        width: 170,
        type: 'number',
        cellClassName: (params) => {
          if (!params.value) {
            return 'missing'
          }
        },
        valueFormatter: (params) => msToMinSec(params),
      })
    })
    return newColumns
  })

  const clickHandler = (start_time, combination) => {
    getVideoByForm(start_time, combination)
    getVideo(start_time, combination).then((data) => {
      toast.error(data.data.data)
    })
  }

  useEffect(() => {
    const positionLabels = ctx.position.map((position) => {
      const optionObject = ctx.positionOptions.find(
        (obj) => obj.value === position
      )
      return optionObject.label
    })

    setSearchPosition(positionLabels.sort().join(','))
  }, [ctx.position, ctx.positionOptions])

  useEffect(() => {
    const form = getForm(ctx.record, columns)
    setExcelData(form)
  }, [ctx.record])

  return (
    <>
      <Container>
        <SearchCondition>
          {ctx.startDate && ctx.endDate && (
            <>
              <Typography>
                日期區間: {dayjs(ctx.startDate).format('YYYY/MM/DD')} ~{' '}
                {dayjs(ctx.endDate).format('YYYY/MM/DD')}
              </Typography>
              <Typography>車道: {searchPosition}</Typography>
              <Typography>查詢時間: {ctx.currentTime}</Typography>
              <Typography>
                只查詢異常: {ctx.isException ? '是' : '否'}
              </Typography>
            </>
          )}
        </SearchCondition>
      </Container>
      <Box sx={{ 'height': 550, '& .missing': { color: 'red' } }}>
        <MTable
          columns={columns}
          rows={ctx.record}
          pageSize={10}
          rowsPerPageOptions={[10]}
          sortingOrder={['desc', 'asc']}
          initialState={{
            sorting: {
              sortModel: [{ field: 'start_time', sort: 'desc' }],
            },
          }}
          loading={ctx.isSubmitting}
          sx={{
            '&.MuiDataGrid-root .MuiDataGrid-cell:focus-within': {
              outline: 'none',
            },
          }}
          components={{
            Toolbar: ExcelButton,
            LoadingOverlay: LinearProgress,
          }}
          componentsProps={{ toolbar: { data: excelData } }}
          rowHeight={120}
        />
      </Box>
    </>
  )
}

const Container = styled(Box)`
  display: flex;
  justify-content: space-between;
  margin-bottom: 24px;
`

const SearchCondition = styled(Box)`
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 100%;
  gap: 4px;
`
